  -webkit-text-overflow: ellipsis;
  -moz-text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.l-line1 {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.l-line2,
.l-line3,
