.label {
    padding: 0px 10px;
    display: inline-block;
    border-radius: 10px;
    background-color: cornflowerblue;
    margin-left: 15px;
    color: #fff;
}
.libraryTitle {
    display: inline-block;
    float: left;
    height: 30px;
    line-height: 30px;
}
.schoolItem {
    float: left;
    list-style: none;
    width: 50%;
    line-height: 30px;
}
.schoolItem > div {
    display: inline-block;
    margin-left: 10px;
}
.schoolItem .displayPic {
    margin-left: 10px;
    text-decoration: underline;
    cursor: pointer;
}
.qrcode{
    width: 300px;
    display: block;
    margin: 20px auto;
    border: 1px solid #E0E0E0;
    border-radius: 12px;
}
.ant-layout-sider-children{
    /* width:250px;
    height:1080px;
    background:rgba(24,43,92,1); */
}
.top {
    /* width: 1135px; */
    height: 230px;
    padding: 15px 25px;
    background:rgba(255,255,255,1);
    box-shadow: 2px 2px 4px 0px rgba(218,218,218,0.5);
}
.top .gardenInfo {
    height: 35px;
    display: flex;
}
.top .gardenInfo p:nth-child(2){
    margin-left: 20px;
    color:rgba(63,148,253,1);
    cursor: pointer;
}
.top .garden {
    padding-left: 7px;
}
.rightL {
    margin-left: 14px;
}
.garden .ant-row {
    margin-top: 3px;
    display: flex;
    align-items: center;
}
.ant-row img {
    margin-top: -6px;
}
.ant-col-8 {
    color:rgba(153,153,153,1);
    /* width: 100%; */
}
.mcode {
    width: 100%;
}
.mcode>div {
    display: flex;
    align-items: center;
}
.mcode img {
    width: 100px;
    height: 37px;
    margin-left: 700px;
}
.member {
    display: flex;
    padding-left: 7px;
    margin-top: 20px;
}
.member dl {
    width: 20%;
    text-align: center;
    border-right: 1px solid #ccc;
}
.member dl:nth-last-child(1) {
    border-right: none;
}
.member dl>dt {
    display: flex;
    align-items: center;
    justify-content: center;
}
.member dl>dt>img{
    width: 15px;
    height: 15px;
    margin-left: 5px;
}
.member dl>dt>span {
    font-size: 13px;
    color:rgba(70,70,70,1);
}
.member dl>dd {
    color:rgba(102,102,102,1);
    margin-top: 5px;
    font-size: 17px;
}
.reader {
    margin-top: 10px;
    height: 120px;
}
.reader>p {
    margin-left: 25px;
}
.reader .content {
    height: 25px;
    display: flex;
}
.content  .infor{
    background:rgba(255,255,255,1);
    box-shadow:4px 4px 10px 0px rgba(218,218,218,0.5);
    width: 20%;
    height: 80px;
    margin-right: 4px;
    /* cursor: pointer; */
}
/* .content  .infor:hover {
    background: rgba(0,0,0,.1);
} */
.content .infor>div:nth-child(1) {
    padding: 5px;
}
.content .infor>div:nth-child(1) img {
    float: right;
    width: 15px;
    height: 15px;
}
.content .infor>div:nth-child(2) dl {
    display: flex;
}
.content .infor>div:nth-child(2) dl>dt {
    padding: 16px 20px;
}
.content .infor>div:nth-child(2) dl>dt>img{
    width: 30px;
    height: 30px;
}
.content .infor>div:nth-child(2) dl>dd {
    margin-top: 7px;
}
.content .infor>div:nth-child(2) dl>dd span {
    display: block;
}
.content .infor>div:nth-child(2) dl>dd span:nth-child(1) {
    color:rgba(102,102,102,1);
    font-size: 16px;
}
.content .infor>div:nth-child(2) dl>dd span:nth-child(2) {
    color:rgba(153,153,153,1);
    font-size: 12px;
    margin-top: 3px;
}
.classroom {
    background: #fff;
    margin-top: 12px;
    padding: 15px 25px;
}
.classroom .addClassroom {
    height: 35px;
    display: flex;
}
.classroom .addClassroom p:nth-child(2){
    margin-left: 20px;
    color:rgba(63,148,253,1);
}
.classroom .ant-table-thead {
    background: #fff;
}
.pwdDialog .ant-form-item {
    display: flex;
}
.librarySet {
    margin-left: 5px;
    width: 100%;
    height: 260px;
    border: 1px solid #ccc;
}
.librarySet .btn {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2px;
}
.librarySet .ant-table-tbody td{
    /* width: 300px; */
}
